import React, { Component } from 'react';
import { connect } from 'react-redux'
import SocketTabItem from '../../components/socket-tab-items/socketTabItems'
import './style.css';

const data = [
    {id:0, url:'https://socket-io-chat.now.sh/', event: [{ name: 'new message', status: true }]},
    //{id:0, url:'http://10.10.30.110:4000/', event: [{ name: 'testSocketAck', status: true }]},
    // { id: 0, url: 'http://10.10.30.110:8086', event: [{ name: 'ajitres', status: true }] },
    //{ id: 1, url: 'http://10.10.30.110:9000/', event: [{ name: 'startRoomAck', status: true },{ name: 'broadcast', status: true },] },    
]

class Dashboard extends Component {
    state = { DataVal: data, active: '' };

    componentDidMount() {
      
      if( this.state.DataVal.lenght > 0)
      { this.setState({ active: this.state.DataVal[0].id}) }
      else
      { this.addNewTab() }

    }

    // remove Tab 
    removeTab = (item) => {
        let data = this.state.DataVal;                
        let index = data.findIndex(val => val.id === item.id)
        data.splice(index, 1)
        this.setState({ DataVal: data })        
        if (data.findIndex(val => val.id === this.state.active) === -1) {
            let activeItem = data.length > 1 ? data[index - 1].id : data[0].id;
            setTimeout(() => this.activeTab(activeItem), 100);
        }
        else {
            let currentTab = data[data.findIndex(val => val.id === this.state.active)].id;
            setTimeout(() => this.activeTab(currentTab), 100);
        }
    }

    // Add new Tab
    addNewTab = () => {
        let data = this.state.DataVal;
        let id = Math.random();
        data.push({ url: '', event: [], id: id })
        this.setState({ DataVal: data, active: id })
    }

    // Update data
    updateData = (item) => {
        // console.log(item);
        let data = this.state.DataVal;
        let index = data.findIndex(val => val.id === item.id)
        data[index].url = item.url;
        this.setState({ DataVal: data })
    }

    // Active tab
    activeTab=(id)=> this.setState({ active: id })

    render() {
        const { DataVal, active } = this.state;
        return (
            <div className="container-fluid pt-2 pb-2">
                <div className="card border-0">
                    <div className="card-header p-0 shadow-none bg-light">
                        <div className="tabboxs d-flex flex-row justify-content-start align-items-stretch">
                            <ul className="tabs d-flex flex-nowrap m-0 p-0">
                                {DataVal.map((item, index) => <li onClick={() => this.activeTab(item.id)} className={(active === item.id ? 'active' : '') + " d-flex"} key={item.id} title={item.url || 'Socket Url'}>
                                    <span>{item.url || 'Socket Url'}</span>
                                    {DataVal.length > 1 ? <i className="material-icons" onClick={() => this.removeTab(item)}>clear</i>:''}
                                </li>)}
                            </ul>
                            <div className="addtabs">
                                <i className="bg-silver text-white material-icons" onClick={this.addNewTab}>add</i>
                            </div>
                        </div>
                    </div>
                    <div className="card-body px-0 py-3">
                        {DataVal.map((item, index) =>
                            <SocketTabItem data={item} show={active} update={this.updateData} key={item.id} className="customshow" socketName={'socket'+item.id}/>
                        )}
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        global: state.Global,
    };
}
export default connect(mapStateToProps)(Dashboard);