
import React, { Component } from 'react';
import { connect } from 'react-redux'
import ReactJson from 'react-json-view'
import io from 'socket.io-client';
import './style.css';

class SocketTabItem extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      data: {}, status: false, newevent: '', Url: '', error: '', sendEvent:'', messageText:'', message:[],  socketUrl:'', sendEvError:false, connectError:false
    };    
    this.socket = this.props.socketName;
  }

  componentDidMount() {
    this.setState({data:this.props.data, Url:this.props.data.url});
  }
 
  UNSAFE_componentWillReceiveProps(props){
    if(props.show !== this.state.data.id)
      this.setState({status: false})
    return true
  }


  startSocket = (url) => {
    if(url !== this.state.socketUrl && this.state.socketUrl !== ''){
      this.socket.close();
      this.setState({ message: [] });
    }
    this.setState({socketUrl:url});
    this.socket = io(url, {transports: ['websocket']});
    this.socket.on('connect', async (message)=> {
      this.setState({ status: true, connectError:false});
    });

    this.socket.on('error', (data) => { console.log(data); this.setState({ error: data, status: false }) });
    this.socket.on('desconnect', (data) => {console.log(data); this.setState({ error: data, status: false }) });
    this.socket.on('connect_error', (data) => {console.log(data); this.setState({ error: data, status: false }) });
    this.connectEvent(this.state.data.event)

    //change tab name
    this.saveSocket()
  }

  connectEvent=(data)=>{
    this.setState({sendEvError:false});
    data.map(item=>{
      if(item.status === true)
      {return this.socket.on(item.name, async (data) =>{ 
        let isJson = this.isJson(data);
        let val = this.state.message;
        val.push({text:isJson? this.DataReturn(data):data, eventName:item.name, is_JSON:isJson, type:'receiver'})
        this.setState({message:val})
        this.scrollDown()
      });
      }
      else return true;
    })
  }

  // this.DataReturn(data)
  DataReturn(val) {
    if(typeof(val) === 'object'){
      return val
    }
    return new Function("return " + val)();
  };

  isJson=(str)=>{
    try { 
      if(typeof(str) === 'object'){
        return true
      }
      let dataval = (new Function("return [" + str+ "];")())
      return typeof(dataval) === 'object' ? true: false;
    } catch (e) {
      return false;
    }
  }

  addEvent = (item) => {
    this.setState({message:[]})
    const is_active = this.socket ? this.socket.connected || this.state.status || this.state.message.length !== 0 : false;
    if(is_active)
    {
      this.setState({connectError:false})
      let values = this.state.data;
      if(values.event.find(datares=> datares.name === item))
      {return alert('event name already added')}

      values.event.push({ name: item, status: true })
      this.setState({ data: values, newevent: '' })
      this.connectEvent(values.event)
      // this.socket.on(item, async (data) =>{ 
      //   console.log('add event connect',data)
      //   let val = this.state.message;
      //   val.push({text:data, eventName:item, sendEvent:this.state.sendEvent})
      //   this.setState({message:val})
      // });
    }
    else{
      this.setState({connectError:true})
    }
  }

  handler = (e) => this.setState({ [e.target.name]: e.target.value });
  removeItem = (item) => {
    let values = this.state.data;
    let index = values.event.findIndex(val => val.name === item.name)
    values.event.splice(index, 1)
    this.setState({ data: values, message:[]})
    this.socket.close();
    this.startSocket(this.state.Url);
  }

  saveSocket = () => {
    let data = this.state.data;
    data.url = this.state.Url;
    this.props.update(data);
  }

  // Send message
  sendMesssage=()=>{
    const is_active = this.socket ? this.socket.connected || this.state.status || this.state.message.length !== 0 : false;
    if(this.state.sendEvent && this.state.messageText !== '' && is_active)
    {
      this.setState({sendEvError:false, connectError:false})
      this.socket.emit(this.state.sendEvent, this.state.messageText);
      let isJson = this.isJson(this.state.messageText)
      let val = this.state.message;
      val.push({text:isJson? this.DataReturn(this.state.messageText):this.state.messageText,sendEvent:this.state.sendEvent, is_JSON:isJson, type:'sender'})
      this.setState({message:val})
      this.scrollDown()
    }
    else {
      is_active ? this.setState({sendEvError:true}):this.setState({connectError:true})
    }
  }

  clearAll=()=>{
    this.setState({sendEvent:'', messageText:'', sendEvError:false})
  }

  changeEventStatus=(item, val)=>{
    let values = this.state.data;
    let index = values.event.findIndex(val => val.name === item.name)
    values.event[index].status = val.target.checked;
    this.setState({data:values, message:[]})
    this.closeSocket()
    this.startSocket(this.state.Url);
  }

  scrollDown=()=>{
    setTimeout(()=>document.getElementById("scroll").scrollIntoView(false),100)
  }

  closeSocket=()=>{
    const is_active = this.socket ? this.socket.connected || this.state.status || this.state.message.length !== 0 : false;
    if(is_active)
      this.socket.close();
    this.setState({ status: false,  message:[]});
  }

  componentWillUnmount(){
    this.closeSocket()
  }

  render() {
    const { data, status, newevent, Url, error, sendEvent, messageText, message, sendEvError, connectError} = this.state;
    const { show, socketName } = this.props;
    const event = data.event ? data.event : [];
    const is_active = this.socket ? this.socket.connected || status || message.length !== 0 : false;
    return (
      <div className={(show === data.id ? '' : 'd-none') + " tabitem"}>
        <div className="container-fluid">
          <div className="row">
            <div className="col">
              <label htmlFor={"socketurl"+socketName} className="m-0 d-none"></label>
              <div className="input-group mb-1">
                <div className="input-group-prepend">
                  <div className={(connectError?"border-danger":'')+" input-group-text"} id="btnGroupAddon">
                    <i className={(is_active ? 'text-success' : 'text-gray') + " material-icons"}>signal_wifi_4_bar</i>
                    {/* <!-- sync_alt --> */}
                  </div>
                </div>
                <input id={"socketurl"+socketName} type="text" className={(connectError?"border-danger":'')+" form-control"} placeholder="Socket Url" value={Url} name="Url" onChange={this.handler} />
                <div className="input-group-append">
                  {is_active ? <button className="btn btn-silver" type="button" id="button-addon2" onClick={() => this.closeSocket()}>Disconnect</button> : <button className="btn btn-primary" type="button" id="button-addon2" onClick={() => this.startSocket(Url)}>Connect</button>}
                  {/* <button className="btn btn-primary" type="button" id="button-addon2" onClick={() => this.saveSocket(data)}>Save</button> */}
                </div>
              </div>
            </div>
          </div>
          <div className={(status ? '' : '') + " row mx-n1"}>
            {/*d-none <!-- Left side --> */}
            <div className="col-sm-4 col-md-3 p-1">
              <div className="p-2 bg-light leftside shadow-sm">
                <div className="form-group">
                  <label htmlFor={"senedvent"+socketName}>Send Message</label>
                  <input id={"senedvent"+socketName} className={(sendEvError?"border-danger":"") +" form-control"} placeholder="Event Name" value={sendEvent} name="sendEvent" onChange={this.handler} autoComplete="false"/>                  
                </div>
                <div className="form-group">
                  <label htmlFor={"messagetxt"+socketName}>Message</label>
                  <textarea id={"messagetxt"+socketName} className={(sendEvError?"border-danger":"") +" form-control"} rows="5" placeholder="Message" name="messageText" value={messageText} onChange={this.handler}></textarea>
                  {sendEvError&&<div className="invalid-feedback d-block">Please enter valid value in send event or message field</div>}
                </div>
                <div className="row mx-n1 ">
                  <div className="col-8 col-sm-7 mb-1 p-1">
                    <button className="btn btn-primary btn-block px-1" onClick={this.sendMesssage}>Send Message</button>
                  </div>
                  <div className="col-4 col-sm-5 mb-1 p-1">
                    <button className="btn btn-danger btn-block" onClick={this.clearAll}>Clear</button>
                  </div>
                  <div className="col-12 p-1">
                    <button className="btn btn-white text-danger p-0" onClick={()=>this.setState({message:[]})}>Clear all message</button>
                  </div>
                </div>

                <hr className="my-1"/>
                <label htmlFor={"recevent"+socketName}>Receiver Event</label>
                <div className="input-group ">
                  <input id={"recevent"+socketName} type="text" className="form-control" value={newevent} name="newevent" placeholder="Event Name" onChange={this.handler} />
                  <div className="input-group-append">
                    <button className="btn btn-silver" type="button" onClick={() => this.addEvent(newevent)}>Add</button>
                  </div>
                </div>
                <ul className="list-group mt-2">
                  {event.map((item, index) =>
                    <li className="list-group-item d-flex justify-content-between align-items-center p-2" key={index}>
                      <div className="d-flex justify-content-between">
                        <label className="m-0 checkboxItem">
                            <input type="checkbox" defaultChecked={item.status} onChange={(e)=>this.changeEventStatus(item, e)}/>
                            <i className="material-icons">stop</i>
                        </label>
                        {item.name}
                      </div>
                      <span className="d-flex cursor"><i className="material-icons" onClick={() => this.removeItem(item)}>clear</i></span>
                    </li>
                  )}
                </ul>
              </div>
            </div>

            {/* <!-- Right side --> */}
            <div className="col-sm-8 col-md-9 p-1 ">
              <div className="p-2 bg-light shadow-sm h-100">
                <div className="mesgs">
                  {message.map((item,index)=>{
                  if(item.type === "receiver"){
                  return <div className="incoming_msg" key={index}>
                    <div className="userImage">
                      <span>{'RE'}</span>
                    </div>
                    <div className="msgbox">
                      {item.is_JSON ? <pre><ReactJson src={item.text} collapsed={true} /></pre>:
                      <pre><code>{item.text}</code></pre>}
                      <span className="info_data">{item.eventName}</span> </div>
                  </div>
                  }
                  else{
                    return <div className="outgoing_msg" key={index}>
                      <div className="sent_msg">
                          {item.is_JSON ? <pre><ReactJson src={item.text} collapsed={true} /></pre>:
                          <pre><code>{item.text}</code></pre>}
                          <span className="info_data">{item.sendEvent}</span> </div>
                        <div className="userImage">
                          <span >{'SE'}</span>
                        </div>
                    </div>
                  }
                  })}
                  {!is_active ? <pre className="alert alert-danger" role="alert">{JSON.stringify(error || "Call Socket Url")}</pre>:''}
                  <div id="scroll"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    global: state.Global,
  };
}
export default connect(mapStateToProps)(SocketTabItem);
